/**************************************
 * Pikaday
 **************************************/
.pika-single {
  font-family: Lato, "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-size: 13px;
  font-weight: 400;
  color: #494949;
  letter-spacing: .01em;
  line-height: 20px;
  box-sizing: border-box !important;
  background: #fff;
  border: 1px solid #bdc3c7;
  border-bottom-color: #999ea4;
  color: #464a4f;
  display: block;
  position: relative;
  z-index: 9999; }
  .pika-single *,
  .pika-single *::before,
  .pika-single *::after {
    box-sizing: border-box !important; }
  .pika-single.is-hidden {
    display: none; }
  .pika-single.is-bound {
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
    margin-top: 5px;
    position: absolute; }

.pika-lendar {
  padding: 10px; }

.pika-title {
  margin-bottom: 10px;
  position: relative;
  text-align: center; }
  .pika-title select {
    cursor: pointer;
    left: 0;
    margin: 0;
    opacity: 0;
    position: absolute;
    top: 5px;
    z-index: 9998; }

.pika-label {
  background-color: #fff;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  line-height: 20px;
  overflow: hidden;
  padding: 5px 15px 5px 5px;
  position: relative;
  z-index: 9999; }
  .pika-label::after {
    border-right: 4px solid transparent;
    border-top: 4px solid #494949;
    border-left: 4px solid transparent;
    content: '';
    right: 4px;
    position: absolute;
    top: 13px; }

.pika-button,
.pika-next,
.pika-prev {
  background: none;
  border: 0;
  outline: none; }

.pika-prev,
.pika-next {
  cursor: pointer;
  display: block;
  height: 30px;
  overflow: hidden;
  /* hide text using text-indent trick. */
  /* negative because firefox indents pseudo-elements when text-indent is positive. 999 because i18n consideration */
  text-indent: -999px;
  white-space: nowrap;
  width: 20px; }
  .pika-prev.is-disabled,
  .pika-next.is-disabled {
    cursor: default;
    opacity: .2; }

.pika-prev {
  float: left; }
  .pika-prev:before {
    border-bottom: 4px solid transparent;
    border-right: 4px solid #494949;
    border-top: 4px solid transparent;
    content: '';
    left: 10px;
    position: absolute;
    top: 10px; }

.pika-next {
  float: right; }
  .pika-next:after {
    border-bottom: 4px solid transparent;
    border-left: 4px solid #494949;
    border-top: 4px solid transparent;
    border-radius: 2px;
    content: '';
    position: absolute;
    right: 10px;
    top: 10px; }

.pika-select {
  display: inline-block; }

.pika-table abbr {
  font-weight: bold; }

.pika-table th {
  color: #0061d5;
  font-size: 13px;
  text-align: center;
  width: 32px; }

.pika-table th,
.pika-table td {
  width: 14.285714285714286%; }

.pika-table > thead > tr {
  height: 25px;
  vertical-align: top; }

.pika-table > tbody > tr {
  height: 33px; }

.pika-button {
  font-size: 13px;
  height: 30px;
  margin: 3px;
  text-align: center;
  width: 30px; }

.is-today .pika-button {
  font-weight: bold; }

.pika-button:hover,
.is-selected .pika-button {
  background-color: #0061d5;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  font-weight: bold; }

.is-disabled .pika-button,
.is-outside-current-month .pika-button {
  cursor: default;
  opacity: .3;
  pointer-events: none; }

/**************************************
 * Date Picker
 **************************************/
.date-picker-wrapper {
  margin: 0 0 20px;
  position: relative; }
  .date-picker-wrapper button:focus {
    outline: none; }
  .date-picker-wrapper .date-picker-icon-holder {
    display: inline-block;
    position: relative; }
  .date-picker-wrapper .date-picker-description {
    color: #777;
    display: block; }
  .date-picker-wrapper .date-picker-input {
    margin-bottom: 0;
    margin-top: 5px;
    padding-right: 30px;
    text-overflow: ellipsis; }
  .date-picker-wrapper .date-picker-input:focus ~ .tooltip,
  .date-picker-wrapper .date-picker-unix-time-input {
    display: none; }
  .date-picker-wrapper .date-picker-open-btn {
    bottom: 10px;
    display: inline-block;
    height: 17px;
    position: absolute;
    right: 7px; }
  .date-picker-wrapper .date-picker-clear-btn,
  .date-picker-wrapper .date-picker-icon-alert {
    bottom: 10px;
    display: inline-block;
    position: absolute;
    right: 30px; }
    .date-picker-wrapper .date-picker-clear-btn span,
    .date-picker-wrapper .date-picker-icon-alert span {
      display: inline-block; }
  .date-picker-wrapper .date-picker-clear-btn,
  .date-picker-wrapper .date-picker-clear-btn:active,
  .date-picker-wrapper .date-picker-clear-btn:focus,
  .date-picker-wrapper .date-picker-clear-btn:hover,
  .date-picker-wrapper .date-picker-clear-btn:active:focus,
  .date-picker-wrapper .date-picker-clear-btn:active:hover,
  .date-picker-wrapper .date-picker-clear-btn span {
    height: 14px; }
  .date-picker-wrapper .date-picker-clear-btn path {
    fill: #afafaf;
    transition: fill linear .1s; }
  .date-picker-wrapper > .date-picker-clear-btn:hover path,
  .date-picker-wrapper > .date-picker-clear-btn:focus path {
    fill: #494949;
    path: #494949; }
  .date-picker-wrapper > .date-picker-open-btn:disabled {
    cursor: default; }
  .date-picker-wrapper > .date-picker-open-btn:hover:enabled .fill-color,
  .date-picker-wrapper > .date-picker-open-btn:focus:enabled .fill-color {
    fill: #494949; }
  .date-picker-wrapper .date-picker-icon-alert path {
    fill: #ef4836; }
  .date-picker-wrapper.show-clear-btn.show-error .date-picker-clear-btn,
  .date-picker-wrapper.show-clear-btn.show-error .date-picker-clear-btn:hover,
  .date-picker-wrapper.show-clear-btn.show-error .date-picker-clear-btn:active {
    right: 25px; }
  .date-picker-wrapper.show-clear-btn.show-error .date-picker-input {
    padding-right: 50px; }
